				var arrs=[]
				var objs=[]
				for(var i=year;i>1944;i--){
					var obj={}
					arrs[i]=i+'年'
					obj.value=arrs[i]
					obj.index=year-i
					objs.push(obj)
				}
				this.timeList=objs
				console.log('返回的时间啊啊啊啊啊啊啊啊啊啊',objs)
				return objs
